import React from 'react';
import './address-form.scss';
import { Button } from '../../../forms/button';
import classNames from 'classnames';

export function AddressForm({ onSubmit }: { onSubmit: (data: any) => void }) {
  return (
    <form className="address-form" onSubmit={(e) => { e.preventDefault(); onSubmit({}); }}>
      <h3 className="address-form-title">Shipping Address</h3>
      
      <div className="address-form-grid">
        <div className="address-form-field">
          <label>First Name</label>
          <input type="text" placeholder="John" required />
        </div>
        
        <div className="address-form-field">
          <label>Last Name</label>
          <input type="text" placeholder="Doe" required />
        </div>
        
        {/*   styles.field, styles.full  String  */}
        <div className={classNames('address-form-field', 'address-form-full')}>
          <label>Address</label>
          <textarea placeholder="House No, Street, District..." rows={3} required />
        </div>
        
        <div className="address-form-field">
          <label>Postal Code</label>
          <input type="text" placeholder="10110" required />
        </div>
        
        <div className="address-form-field">
          <label>Phone Number</label>
          <input type="tel" placeholder="08x-xxx-xxxx" required />
        </div>
      </div>
      
      <Button 
        type="submit" 
        fullWidth 
        appearance="primary" 
        size="lg" 
        className="address-form-submit-btn"
      >
        CONTINUE TO PAYMENT
      </Button>
    </form>
  );
}