import React, { useState } from 'react';
import classNames from 'classnames';
import type { TextInputProps } from './text-input.type';
import { Icon } from '../../../icon/icon';
import './text-input.scss';

export function TextInput({
  id,
  value,
  onChange,
  disabled,
  error,
  placeholder,
  label,
  type = 'text',
  radius = 'sm',
  leadingIcon,
  trailingIcon,
  className,
  style,
  ...rest
}: TextInputProps) {
  
  //  
  const [showPassword, setShowPassword] = useState(false);
  const isPasswordType = type === 'password';
  const inputType = isPasswordType && showPassword ? 'text' : type;

  const togglePassword = () => setShowPassword(!showPassword);

  const containerClasses = classNames('lm-input-container', `is-radius-${radius}`, {
    'is-error': !!error,
    'is-disabled': disabled,
  });

  return (
    <div className={classNames('lm-input-root', className)} style={style}>
      {label && <label htmlFor={id} className="lm-input-label">{label}</label>}
      
      <div className={containerClasses}>
        {leadingIcon && (
          <div className="lm-input-leading-icon">{leadingIcon}</div>
        )}
        
        <input
          {...rest}
          id={id}
          type={inputType}
          value={value}
          onChange={onChange}
          disabled={disabled}
          placeholder={placeholder}
          className="lm-input-field"
        />
        
        {isPasswordType ? (
          <button 
            type="button"
            className="lm-input-password-toggle"
            onClick={togglePassword}
            tabIndex={-1}
            aria-label={showPassword ? "Hide password" : "Show password"}
          >
            <Icon icon={showPassword ? 'mingcute:eye-close-line' : 'mingcute:eye-line'} size={18} />
          </button>
        ) : trailingIcon && (
          <div className="lm-input-trailing-icon">{trailingIcon}</div>
        )}
      </div>

      {error && <span className="lm-input-error-message">{error}</span>}
    </div>
  );
}