import React from 'react';
import classNames from 'classnames';
import { useTheme } from '../../../theme/theme-controller';
import { SunIcon } from './sun-icon';
import { MoonIcon } from './moon-icon';
import './theme-toggler.scss'; //   .module.scss

export interface ThemeTogglerProps {
  className?: string;
}

export function ThemeToggler({ className }: ThemeTogglerProps) {
  //   themeMode  Theme
  const { themeMode, toggleTheme } = useTheme(); 
  
  const isDark = themeMode === 'dark';

  return (
    <button
      className={classNames('lm-theme-toggler', className)}
      onClick={toggleTheme}
      type="button"
      aria-label={isDark ? "Switch to light mode" : "Switch to dark mode"}
    >
      <div className="lm-theme-toggler-icon-wrapper">
        <SunIcon 
          className={classNames('lm-theme-toggler-icon', 'is-sun', { 'is-active': !isDark })} 
          aria-hidden="true"
        />
        <MoonIcon 
          className={classNames('lm-theme-toggler-icon', 'is-moon', { 'is-active': isDark })} 
          aria-hidden="true"
        />
      </div>
    </button>
  );
}