import React, { useEffect } from 'react';
import classNames from 'classnames';
import './toast.scss'; //   import  .scss

export interface ToastProps {
  id: string;
  message: string;
  type?: 'success' | 'error' | 'info' | 'warning';
  duration?: number;
  onClose: (id: string) => void;
}

export function Toast({ 
  id, 
  message, 
  type = 'info', 
  duration = 3000, 
  onClose 
}: ToastProps) {
  
  useEffect(() => {
    const timer = setTimeout(() => onClose(id), duration);
    return () => clearTimeout(timer);
  }, [id, duration, onClose]);

  return (
    <div className={classNames('lm-toast', `is-${type}`)} role="alert">
      <div className="lm-toast-icon">
        {/*  Icon  type  */}
      </div>
      <div className="lm-toast-message">{message}</div>
      <button 
        className="lm-toast-close-btn" 
        onClick={() => onClose(id)}
        aria-label="Close"
      >
        
      </button>
    </div>
  );
}