import React from 'react';
import classNames from 'classnames';
import './loading.scss';

export interface SpinnerProps {
  size?: 'sm' | 'md' | 'lg' | number;
  color?: string;
  className?: string;
}

export function Spinner({ size = 'md', color, className }: SpinnerProps) {
  const sizeValue = typeof size === 'number' ? size : undefined;

  return (
    <div
      className={classNames(
        'lm-spinner', //   styles.spinner
        typeof size === 'string' ? `is-${size}` : '', //   template literal  styles[size]
        className
      )}
      style={{
        ...(sizeValue ? { width: sizeValue, height: sizeValue } : {}),
        ...(color ? { borderTopColor: color } : {}),
      }}
      role="status"
    >
      <span className="lm-sr-only">Loading...</span>
    </div>
  );
}