import React, { type ReactNode } from 'react';
import classNames from 'classnames';
import { Button } from '../../forms/button';
import { Heading } from '../../typography/heading';
import { Paragraph } from '../../typography/paragraph';
import { Image } from '../image';
import './banner.scss';

export type BannerProps = {
  title?: string | ReactNode;
  subtitle?: string | ReactNode;
  ctaText?: string;
  ctaHref?: string;
  onCtaClick?: () => void;
  action?: ReactNode;
  backgroundImage?: string;
  foregroundImage?: string;
  variant?: 'hero' | 'promotional' | 'alert';
  align?: 'left' | 'center' | 'right';
  overlay?: boolean;
  inverse?: boolean;
  className?: string;
  style?: React.CSSProperties;
  children?: ReactNode;
};

export function Banner({
  title,
  subtitle,
  ctaText,
  ctaHref,
  onCtaClick,
  action,
  backgroundImage,
  foregroundImage,
  variant = 'hero',
  align = 'center',
  overlay = false,
  inverse = false,
  className,
  children,
  style,
  ...rest
}: BannerProps) {
  const isAlert = variant === 'alert';
  const headingLevel = isAlert ? 'h6' : (variant === 'hero' ? 'h1' : 'h2');

  const renderActions = () => {
    if (action) return action;
    if (ctaText) {
      return (
        <Button
          appearance={inverse ? 'secondary' : 'primary'}
          href={ctaHref}
          onClick={onCtaClick}
        >
          {ctaText}
        </Button>
      );
    }
    return null;
  };

  const actionContent = renderActions();

  return (
    <div
      className={classNames(
        'banner',       //   String
        variant,        //   Prop  (hero, promotional, alert)
        align,          //   Prop  (left, center, right)
        {
          'inverse': inverse, //   String
        },
        className
      )}
      style={style}
      {...rest}
    >
      {backgroundImage && (
        <>
          <Image
            src={backgroundImage}
            alt=""
            className="background" //   String
            fit="cover"
          />
          {overlay && <div className="overlay" />} {/*   String */}
        </>
      )}

      <div className="content"> {/*   String */}
        {(title || subtitle) && (
          <div className="text-wrapper"> {/*  :  textWrapper  text-wrapper  */}
            {title && (
              <Heading
                level={headingLevel}
                className="title" //   String
                inverseColor={inverse}
              >
                {title}
              </Heading>
            )}
            {subtitle && (
              <Paragraph
                size={isAlert ? 'body-small' : 'body-large'}
                color={inverse ? 'inverse' : 'primary'}
                className="subtitle" //   String
              >
                {subtitle}
              </Paragraph>
            )}
          </div>
        )}

        {actionContent && (
          <div className="actions"> {/*   String */}
            {actionContent}
          </div>
        )}

        {children}

        {foregroundImage && (
          <div className="foreground-image-wrapper"> {/*   foregroundImageWrapper */}
            <Image
              src={foregroundImage}
              alt="Banner Visual"
              width="auto"
              height="auto"
            />
          </div>
        )}
      </div>
    </div>
  );
}