import React, { useId } from 'react';
import classNames from 'classnames';
import './checkbox.scss';

export interface CheckboxProps extends Omit<React.InputHTMLAttributes<HTMLInputElement>, 'size'> {
  label?: string;
  size?: 'sm' | 'md' | 'lg';
  indeterminate?: boolean;
}

export const Checkbox = React.forwardRef<HTMLInputElement, CheckboxProps>(
  ({ label, size = 'md', indeterminate, className, disabled, ...props }, ref) => {
    const defaultId = useId();
    const id = props.id || defaultId;

    //  Indeterminate  Ref
    React.useEffect(() => {
      if (ref && typeof ref !== 'function' && ref.current) {
        ref.current.indeterminate = !!indeterminate;
      }
    }, [indeterminate, ref]);

    return (
      <div className={classNames('lm-checkbox-container', `is-${size}`, { 'is-disabled': disabled }, className)}>
        <div className="lm-checkbox-input-wrapper">
          <input
            type="checkbox"
            className="lm-checkbox-hidden-input"
            id={id}
            ref={ref}
            disabled={disabled}
            {...props}
          />
          <div className="lm-checkbox-control">
            {/*  */}
            <svg 
              className="lm-checkbox-check-icon" 
              viewBox="0 0 12 10" 
              fill="none" 
              stroke="currentColor" 
              strokeWidth="2.5" 
              strokeLinecap="round" 
              strokeLinejoin="round"
            >
              <polyline points="1.5 5 4.5 8 10.5 2" />
            </svg>
            {/*  */}
            <div className="lm-checkbox-indeterminate-icon" />
          </div>
        </div>
        {label && (
          <label htmlFor={id} className="lm-checkbox-label">
            {label}
          </label>
        )}
      </div>
    );
  }
);

Checkbox.displayName = 'Checkbox';