import {
  createContext,
  useContext,
  useState,
  useCallback,
  useEffect,
  type ReactNode,
} from 'react';

export type ThemeMode = 'light' | 'dark';

export interface ThemeContextValue {
  themeMode: ThemeMode; //   theme  themeMode  jpjoy-theme.tsx
  toggleTheme: () => void;
  setThemeMode: (mode: ThemeMode) => void; //  
}

//  Context
export const ThemeContext = createContext<ThemeContextValue | undefined>(undefined);

/* =========================
   ThemeProvider
========================= */
export function ThemeProvider({
  children,
  initialTheme = 'light',
}: {
  children: ReactNode;
  initialTheme?: ThemeMode;
}) {
  //  LocalStorage   initialTheme
  const [themeMode, setThemeModeState] = useState<ThemeMode>(() => {
    if (typeof window !== 'undefined') {
      const saved = localStorage.getItem('jpjoy-theme-mode') as ThemeMode;
      return saved || initialTheme;
    }
    return initialTheme;
  });

  //  Attribute  <html>  LocalStorage
  const updateTheme = (mode: ThemeMode) => {
    document.documentElement.dataset.theme = mode;
    localStorage.setItem('jpjoy-theme-mode', mode);
  };

  const setThemeMode = useCallback((mode: ThemeMode) => {
    setThemeModeState(mode);
    updateTheme(mode);
  }, []);

  const toggleTheme = useCallback(() => {
    setThemeModeState((prev) => {
      const next = prev === 'light' ? 'dark' : 'light';
      updateTheme(next);
      return next;
    });
  }, []);

  // Sync 
  useEffect(() => {
    updateTheme(themeMode);
  }, [themeMode]);

  return (
    <ThemeContext.Provider value={{ themeMode, toggleTheme, setThemeMode }}>
      {children}
    </ThemeContext.Provider>
  );
}

/* =========================
   Public hook
========================= */
export function useTheme(): ThemeContextValue {
  const context = useContext(ThemeContext);
  if (!context) {
    throw new Error('useTheme must be used within ThemeProvider');
  }
  return context;
}