import React, { type ButtonHTMLAttributes, type ReactNode } from 'react';
import classNames from 'classnames';
import { Link, type LinkProps } from '../../navigation/link/link';
import { type ButtonAppearance } from './button-appearance-type';
import './button.scss'; //   .module.scss

export type ButtonSize = 'sm' | 'md' | 'lg';

type CommonButtonProps = {
  children?: ReactNode;
  appearance?: ButtonAppearance;
  size?: ButtonSize;
  className?: string;
  disabled?: boolean;
  style?: React.CSSProperties;
  fullWidth?: boolean;
  loading?: boolean;
};

type ButtonAsButton = CommonButtonProps & {
  href?: undefined;
  external?: undefined;
  type?: ButtonHTMLAttributes<HTMLButtonElement>['type'];
  onClick?: React.MouseEventHandler<HTMLButtonElement>;
} & Omit<ButtonHTMLAttributes<HTMLButtonElement>, 'children' | 'className' | 'disabled' | 'type' | 'onClick'>;

type ButtonAsLink = CommonButtonProps & {
  href: string;
  external?: boolean;
  type?: undefined;
  onClick?: React.MouseEventHandler<HTMLAnchorElement>;
} & Omit<LinkProps, 'children' | 'className' | 'disabled' | 'href' | 'external' | 'onClick'>;

export type ButtonProps = ButtonAsLink | ButtonAsButton;

export function Button({
  children,
  appearance = 'primary',
  size = 'md',
  className,
  disabled,
  style,
  fullWidth,
  loading,
  ...props
}: ButtonProps) {
  
  //   Class String  Prefix
  const buttonClasses = classNames(
    'lm-btn',
    `lm-btn-${appearance}`,
    `lm-btn-${size}`,
    {
      'is-disabled': disabled,
      'is-full-width': fullWidth,
      'is-loading': loading,
    },
    className
  );

  const content = (
    <>
      {loading && <span className="lm-btn-spinner" />}
      <span className="lm-btn-label">{children}</span>
    </>
  );

  if ('href' in props && props.href !== undefined) {
    const { href, external, onClick, ...restOfLinkProps } = props as ButtonAsLink;

    return (
      <Link
        href={(disabled || loading) ? undefined : href}
        className={buttonClasses}
        external={external}
        disabled={disabled || loading}
        onClick={onClick}
        style={style}
        {...(restOfLinkProps as any)}
      >
        {content}
      </Link>
    );
  }

  const { type = 'button', onClick, ...restOfButtonProps } = props as ButtonAsButton;

  return (
    <button
      type={type}
      className={buttonClasses}
      disabled={disabled || loading}
      onClick={onClick}
      style={style}
      {...restOfButtonProps}
    >
      {content}
    </button>
  );
}