import React from 'react';
import classNames from 'classnames';
import './label.scss'; //   CSS Modules  Global SCSS

export type LabelProps = {
  children: React.ReactNode;
  /**  Label */
  variant?: 'default' | 'medium';
  /**  */
  color?: 'primary' | 'secondary';
  /**  */
  className?: string;
  /** HTML Tag  (default: span) */
  as?: 'span' | 'label' | 'p' | 'div';
};

export function Label({
  children,
  variant = 'default',
  color = 'primary',
  className,
  as: Component = 'span',
}: LabelProps) {
  
  //   Class String  Prefix 'lm-label'
  const labelClasses = classNames(
    'lm-label-base',
    `is-variant-${variant}`,
    `is-color-${color}`,
    className
  );

  return <Component className={labelClasses}>{children}</Component>;
}