import React from 'react';
import { Link } from '../../navigation/link';
import classNames from 'classnames';
import './tab-bar.scss'; //   Global SCSS
import type { TabBarItem } from './tab-bar-item-type';

export type TabBarProps = {
  items: TabBarItem[];
  selectedValue?: string;
  onTabChange?: (value: string) => void;
  fullWidth?: boolean;
  className?: string;
  style?: React.CSSProperties;
};

export function TabBar({
  items,
  selectedValue,
  onTabChange,
  fullWidth = false,
  className,
  style,
}: TabBarProps) {
  //   Class String  Prefix 'lm-tab-bar'
  const containerClasses = classNames('lm-tab-bar', {
    'is-full-width': fullWidth,
  }, className);

  return (
    <nav
      className={containerClasses}
      style={style}
      role="tablist"
    >
      {items.map((item) => {
        const isSelected = selectedValue === item.value;

        const handleClick = () => {
          onTabChange?.(item.value);
        };

        const content = (
          <>
            {item.icon && <span className="lm-tab-bar-icon">{item.icon}</span>}
            <span className="lm-tab-bar-label">{item.label}</span>
          </>
        );

        const tabClasses = classNames('lm-tab-bar-item', {
          'is-active': isSelected,
        });

        if (item.href) {
          return (
            <Link
              key={item.value}
              href={item.href}
              onClick={handleClick}
              className={tabClasses}
              role="tab"
              aria-selected={isSelected}
              noStyles
            >
              {content}
            </Link>
          );
        }

        return (
          <button
            key={item.value}
            type="button"
            onClick={handleClick}
            className={tabClasses}
            role="tab"
            aria-selected={isSelected}
          >
            {content}
          </button>
        );
      })}
    </nav>
  );
}