import React, { useState } from 'react';
import {
  useFloating,
  offset,
  flip,
  shift,
  size,
  autoUpdate,
} from '@floating-ui/react';
import { TextInput } from '../text-input/text-input';
import { Calendar } from './calendar';
import { format } from 'date-fns';
import { Icon } from '../../../icon/icon';
import './date-input.scss';

export type DateInputProps = {
  label?: string;
  value?: string;
  onChange?: (value: string) => void;
  placeholder?: string;
};

export function DateInput({
  label,
  value,
  onChange,
  placeholder = 'Select date',
}: DateInputProps) {
  const [open, setOpen] = useState(false);
  const selectedDate = value ? new Date(value) : undefined;

  const { refs, floatingStyles } = useFloating({
    open,
    onOpenChange: setOpen,
    placement: 'bottom-start',
    whileElementsMounted: autoUpdate,
    middleware: [
      offset(4),
      flip(),
      shift({ padding: 8 }),
      size({
        apply({ rects, elements }) {
          Object.assign(elements.floating.style, {
            width: `${Math.max(rects.reference.width, 280)}px`,
            maxWidth: '320px',
          });
        },
      }),
    ],
  });

  return (
    <>
      <div
        ref={refs.setReference}
        className="lm-date-input-container"
        onClick={() => setOpen(true)}
      >
        <TextInput
          label={label}
          type="text"
          readOnly
          value={value ?? ''}
          placeholder={placeholder}
          className="lm-date-input-field"
          style={{ cursor: 'pointer' }}
          trailingIcon={
            <span className="lm-date-input-calendar-icon" aria-hidden>
              <Icon icon="mingcute:calendar-line" size={20} />
            </span>
          }
        />
        {/* Click Overlay */}
        <div className="lm-date-input-overlay" aria-hidden />
      </div>

      {open && (
        <div
          ref={refs.setFloating}
          style={{
            ...(floatingStyles as React.CSSProperties),
            zIndex: 1000,
          }}
          className="lm-date-input-calendar-wrapper"
          role="dialog"
        >
          <div className="lm-date-input-inner-calendar">
            <Calendar
              selected={selectedDate}
              onSelect={(d) => {
                if (d) {
                  onChange?.(format(d, 'yyyy-MM-dd'));
                }
                setOpen(false);
              }}
            />
          </div>
        </div>
      )}
    </>
  );
}