import React from 'react';
import classNames from 'classnames';
import './loading.scss';

export interface SkeletonProps {
  variant?: 'text' | 'circular' | 'rectangular';
  width?: string | number;
  height?: string | number;
  animation?: 'pulse' | 'wave' | 'none';
  className?: string;
  style?: React.CSSProperties;
}

export function Skeleton({
  variant = 'text',
  width,
  height,
  animation = 'pulse',
  className,
  style,
}: SkeletonProps) {
  return (
    <div
      className={classNames(
        'lm-skeleton', //   styles.skeleton
        `is-${variant}`, //   styles[variant]
        `anim-${animation}`, //   styles[animation]
        className
      )}
      style={{ 
        width, 
        height, 
        ...style 
      }}
    />
  );
}