import React, { useState } from 'react';
import classNames from 'classnames';
import './payment-method.scss';

type PaymentType = 'credit' | 'transfer' | 'cod';

export function PaymentMethod() {
  const [selected, setSelected] = useState<PaymentType>('credit');

  return (
    <div className="payment-container"> {/*   container */}
      <h3 className="payment-title">Payment Method</h3>
      
      <div className="payment-list"> {/*   prefix */}
        <button 
          type="button"
          className={classNames('payment-method-item', { 
            'active': selected === 'credit' 
          })}
          onClick={() => setSelected('credit')}
        >
          <span className="mingcute--bank-card-line" />
          <div className="payment-info">
            <span className="payment-name">Credit / Debit Card</span>
            <span className="payment-desc">Visa, Mastercard, JCB</span>
          </div>
        </button>

        <button 
          type="button"
          className={classNames('payment-method-item', { 
            'active': selected === 'transfer' 
          })}
          onClick={() => setSelected('transfer')}
        >
          <span className="mingcute--transfer-line" />
          <div className="payment-info">
            <span className="payment-name">Bank Transfer</span>
            <span className="payment-desc">QR PromptPay, Mobile Banking</span>
          </div>
        </button>
      </div>
    </div>
  );
}