import React, { type ReactNode } from 'react';
import classNames from 'classnames';
import { Logo } from '../../data-display/logo';
import { Link } from '../../navigation/link';
import { IconButton } from '../../forms/icon-button/icon-button';
import { ArrowLeftIcon } from './arrow-left-icon';
import type { HeaderNavItem } from './header-nav-item-type';
import './header.scss'; //   Global SCSS

export type HeaderProps = {
  variant?: 'website' | 'app';
  title?: string;
  navItems?: HeaderNavItem[];
  action?: ReactNode;
  onBack?: () => void;
  rightContent?: ReactNode;
  logoName?: string;
  logoHref?: string;
  customLogo?: ReactNode;
  className?: string;
  style?: React.CSSProperties;
};

export function Header({
  variant = 'website',
  title,
  navItems = [],
  action,
  onBack,
  rightContent,
  logoName = 'LOOKMIX',
  logoHref = '/',
  customLogo,
  className,
  style,
}: HeaderProps) {
  const isWebsite = variant === 'website';
  const isApp = variant === 'app';

  //   Class String  Prefix 'lm-header'
  const headerClasses = classNames('lm-header', `is-${variant}`, className);

  return (
    <header className={headerClasses} style={style}>
      <div className="lm-header-container">
        {isWebsite && (
          <>
            <div className="lm-header-left">
              {customLogo || <Logo name={logoName} href={logoHref} />}
            </div>

            <nav className="lm-header-nav">
              {navItems.map((item, index) => (
                <Link key={index} href={item.href} className="lm-header-nav-link">
                  {item.label}
                </Link>
              ))}
            </nav>

            <div className="lm-header-actions">
              {action}
            </div>
          </>
        )}

        {isApp && (
          <>
            <div className="lm-header-app-left">
              {onBack && (
                <IconButton
                  onClick={onBack}
                  icon={<ArrowLeftIcon />}
                  variant="ghost"
                  size="sm"
                  shape="circle"
                  aria-label="Go back"
                />
              )}
            </div>

            <div className="lm-header-app-center">
              {title && <h1 className="lm-header-app-title">{title}</h1>}
            </div>

            <div className="lm-header-app-right">
              {rightContent}
            </div>
          </>
        )}
      </div>
    </header>
  );
}