import React from 'react';
import classNames from 'classnames';
import type { SegmentedControlOption } from './segmented-control-option-type';
import './segmented-control.scss'; //   .scss 

export type SegmentedControlProps = {
  options?: SegmentedControlOption[];
  value?: string;
  onChange?: (value: string) => void;
  className?: string;
};

const defaultOptions: SegmentedControlOption[] = [
  { label: 'Workwear', value: 'workwear' },
  { label: 'Casual', value: 'casual' },
  { label: 'Formal', value: 'formal' },
  { label: 'Gym', value: 'gym' },
];

export function SegmentedControl({
  options = defaultOptions,
  value = 'workwear',
  onChange,
  className,
}: SegmentedControlProps) {
  const handleOptionClick = (optionValue: string, isDisabled?: boolean) => {
    if (isDisabled) return;
    if (onChange) {
      onChange(optionValue);
    }
  };

  return (
    <div className={classNames('lm-segmented-control', className)}>
      {options.map((option) => {
        const isSelected = option.value === value;
        return (
          <button
            key={option.value}
            type="button"
            className={classNames('lm-segmented-option', {
              'is-active': isSelected,
              'is-disabled': option.disabled,
            })}
            onClick={() => handleOptionClick(option.value, option.disabled)}
            disabled={option.disabled}
            aria-pressed={isSelected}
          >
            {option.label}
          </button>
        );
      })}
    </div>
  );
}