import React from 'react';
import classNames from 'classnames';
import './order-status-timeline.scss';

export type OrderEvent = {
  id: string;
  status: string;
  description: string;
  timestamp: string;
  isCompleted: boolean;
};

export type OrderStatusTimelineProps = {
  events: OrderEvent[];
};

export function OrderStatusTimeline({ events }: OrderStatusTimelineProps) {
  return (
    <div className="timeline-container"> {/*   timeline */}
      {events.map((event, index) => (
        <div 
          key={event.id} 
          className={classNames('timeline-event-item', { //   styles.eventItem
            'completed': event.isCompleted,             //   styles.completed
            'active': event.isCompleted && (index === 0 || !events[index - 1]?.isCompleted)
          })}
        >
          <div className="timeline-indicator"> {/*   prefix */}
            <div className="timeline-dot">
              {event.isCompleted && <span className="mingcute--check-line" />}
            </div>
            {index !== events.length - 1 && <div className="timeline-line" />}
          </div>
          
          <div className="timeline-content">
            <div className="timeline-header">
              <span className="timeline-status-title">{event.status}</span>
              <span className="timeline-time">{event.timestamp}</span>
            </div>
            <p className="timeline-description">{event.description}</p>
          </div>
        </div>
      ))}
    </div>
  );
}