import React from 'react';
import classNames from 'classnames';
import { Button } from '../../forms/button';
import { Link } from '../../navigation/link';
import { Icon } from '../../../icon/icon';
import './footer.scss'; //   CSS Modules  Global SCSS

export type FooterProps = {
  className?: string;
  style?: React.CSSProperties;
};

export function Footer({ className, style }: FooterProps) {
  //   Class String  Prefix 'lm-footer'
  return (
    <footer className={classNames('lm-footer', className)} style={style}>
      <div className="lm-footer-container">
        
        {/* :  */}
        <div className="lm-footer-top">
          <span className="lm-footer-label">Download</span>
          <div className="lm-footer-stores">
            <Button
              appearance="outline"
              className="lm-footer-store-btn"
              href="https://www.apple.com/app-store/"
              external
            >
              <Icon icon="mingcute:apple-fill" size={20} />
              <span>App store</span> {/*   span  alignment */}
            </Button>
            
            <Button
              appearance="outline"
              className="lm-footer-store-btn"
              href="https://play.google.com/store"
              external
            >
              <Icon icon="mingcute:google-play-fill" size={20} />
              <span>Google play</span>
            </Button>
          </div>
        </div>

        <div className="lm-footer-divider" />

        {/* : Copyright, Links  Social Media */}
        <div className="lm-footer-bottom">
          <div className="lm-footer-left-group">
            <span className="lm-footer-copyright">&copy; LY Corporation</span>
            <div className="lm-footer-links">
              <Link href="/help" className="lm-footer-link">Help</Link>
              <Link href="/terms" className="lm-footer-link">User Agreement</Link>
              <Link href="/privacy" className="lm-footer-link">Privacy Policy</Link>
            </div>
          </div>

          <div className="lm-footer-socials">
            <Link href="https://facebook.com" external noStyles>
              <Icon icon="mingcute:facebook-fill" size={24} className="lm-footer-social-icon" />
            </Link>
            
            <Link href="https://instagram.com" external noStyles>
              <Icon icon="mingcute:ins-line" size={24} className="lm-footer-social-icon" />
            </Link>
            
            <Link href="https://youtube.com" external noStyles>
              <Icon icon="mingcute:youtube-fill" size={24} className="lm-footer-social-icon" />
            </Link>
          </div>
        </div>
      </div>
    </footer>
  );
}