import React from 'react';
import classNames from 'classnames';
import './badge.scss'; //  Global ()

export interface BadgeProps {
  content?: string | number;
  variant?: 'primary' | 'secondary' | 'success' | 'danger' | 'warning';
  size?: 'sm' | 'md';
  isDot?: boolean;
  max?: number;
  children?: React.ReactNode;
  className?: string;
  style?: React.CSSProperties;
}

export function Badge({
  content,
  variant = 'primary',
  size = 'md',
  isDot = false,
  max = 99,
  children,
  className,
}: BadgeProps) {
  
  const displayContent = typeof content === 'number' && content > max ? `${max}+` : content;

  const badgeElement = (
    <span
      className={classNames(
        'badge',          //   styles.badge  string
        variant,          //   styles[variant]  variant  ( 'primary')
        size,             //   styles[size]  size  ( 'md')
        {
          'dot': isDot,   //   string
          'overlap': !!children, 
        },
        className
      )}
    >
      {!isDot && displayContent}
    </span>
  );

  if (!children) {
    return badgeElement;
  }

  return (
    <div className="badge-container"> {/*   styles.badgeContainer  'badge-container' */}
      {children}
      {badgeElement}
    </div>
  );
}