import React from 'react';
import classNames from 'classnames';
import './property-selector.scss';

export type PropertyOption = {
  label: string;
  value: string;
  disabled?: boolean;
  color?: string; // 
};

export type PropertySelectorProps = {
  label: string;
  options: PropertyOption[];
  selectedValue?: string;
  onChange: (value: string) => void;
  type?: 'text' | 'color';
};

export function PropertySelector({ 
  label, 
  options, 
  selectedValue, 
  onChange, 
  type = 'text' 
}: PropertySelectorProps) {
  return (
    <div className="prop-selector-container">
      <span className="prop-selector-label">{label}</span>
      <div className="prop-selector-options-grid">
        {options.map((option) => (
          <button
            key={option.value}
            type="button"
            disabled={option.disabled}
            onClick={() => onChange(option.value)}
            className={classNames('prop-selector-option', {
              'active': selectedValue === option.value,
              'is-color-type': type === 'color', //  styles.colorType
            })}
            title={option.label}
          >
            {type === 'color' ? (
              <span 
                className="prop-selector-color-circle" 
                style={{ backgroundColor: option.color }} 
              />
            ) : (
              option.label
            )}
          </button>
        ))}
      </div>
    </div>
  );
}