import React from 'react';
import './price-summary.scss';
import { Button } from '../../../forms/button/button';
import classNames from 'classnames';

export type PriceSummaryProps = {
  subtotal: number;
  shippingFee: number;
  discount?: number;
  onCheckout: () => void;
  isLoading?: boolean;
};

export function PriceSummary({
  subtotal,
  shippingFee,
  discount = 0,
  onCheckout,
  isLoading
}: PriceSummaryProps) {
  const total = subtotal + shippingFee - discount;

  return (
    <div className="price-summary"> {/*   summaryCard */}
      <h3 className="price-summary-title">Order Summary</h3>
      
      <div className="price-summary-rows">
        <div className="price-summary-row">
          <span>Subtotal</span>
          <span>{subtotal.toLocaleString()}</span>
        </div>
        
        <div className="price-summary-row">
          <span>Shipping Fee</span>
          <span>{shippingFee === 0 ? 'Free' : `${shippingFee.toLocaleString()}`}</span>
        </div>

        {discount > 0 && (
          //   styles.row, styles.discount  String 
          <div className={classNames('price-summary-row', 'price-summary-discount')}>
            <span>Discount</span>
            <span>-{discount.toLocaleString()}</span>
          </div>
        )}

        <hr className="price-summary-divider" />
        
        {/*   styles.row, styles.totalRow  String  */}
        <div className={classNames('price-summary-row', 'price-summary-total-row')}>
          <span>Total</span>
          <span>{total.toLocaleString()}</span>
        </div>
      </div>

      <Button 
        fullWidth 
        size="lg" 
        onClick={onCheckout}
        loading={isLoading}
        className="price-summary-checkout-btn"
      >
        PROCEED TO CHECKOUT
      </Button>
    </div>
  );
}