import React, { type ReactNode } from 'react';
import classNames from 'classnames';
import { Image } from '../image';
import { Heading } from '../../typography/heading';
import './card.scss';

export type CardVariant = 'default' | 'glass' | 'compact';

export type CardProps = {
  children?: ReactNode;
  title?: string;
  header?: ReactNode;
  footer?: ReactNode;
  image?: string;
  imageAlt?: string;
  variant?: CardVariant;
  interactive?: boolean;
  className?: string;
  style?: React.CSSProperties;
  onClick?: () => void;
} & React.HTMLAttributes<HTMLDivElement>;

export function Card({
  children,
  title,
  header,
  footer,
  image,
  imageAlt,
  variant = 'default',
  interactive = false,
  className,
  onClick,
  style,
  ...rest //  props 
}: CardProps) {
  return (
    <div
      className={classNames(
        'card',         //   String 
        variant,        //   variant  ( 'glass')
        {
          'interactive': interactive, //   interactive  true
        },
        className
      )}
      onClick={interactive ? onClick : undefined}
      style={style}
      {...rest} //   ...rest ()  {...rest}  Spread attributes  div
    >
      {image && (
        <div className="card-image-wrapper">
          <Image
            src={image}
            alt={imageAlt || ''}
            width="100%"
          />
        </div>
      )}
      
      {header && <div className="card-header">{header}</div>}

      <div className="card-content">
        {title && (
          <Heading level="h4" className="card-title">
            {title}
          </Heading>
        )}
        {children}
      </div>

      {footer && <div className="card-footer">{footer}</div>}
    </div>
  );
}