import React from 'react';
import './cart-item.scss';
import { QuantityStepper } from '../../quantity-stepper/quantity-stepper';

export type CartItemProps = {
  id: string;
  title: string;
  image: string;
  price: number;
  properties?: { label: string; value: string }[];
  quantity: number;
  onQuantityChange: (id: string, quantity: number) => void;
  onRemove: (id: string) => void;
};

export function CartItem({
  id,
  title,
  image,
  price,
  properties,
  quantity,
  onQuantityChange,
  onRemove,
}: CartItemProps) {
  return (
    <div className="cart-item"> {/*   item */}
      <div className="cart-item-image-container"> {/*  kebab-case + prefix */}
        <img src={image} alt={title} className="cart-item-image" />
      </div>

      <div className="cart-item-details">
        <div className="cart-item-header">
          <h4 className="cart-item-title">{title}</h4>
          <button 
            className="cart-item-remove-btn" 
            onClick={() => onRemove(id)}
            aria-label="Remove item"
          >
            <span className="mingcute--delete-2-line" />
          </button>
        </div>

        {properties && properties.length > 0 && (
          <div className="cart-item-properties">
            {properties.map((p, i) => (
              <span key={i} className="cart-item-prop-text">
                {p.label}: <strong>{p.value}</strong>
              </span>
            ))}
          </div>
        )}

        <div className="cart-item-footer">
          <span className="cart-item-price">{(price * quantity).toLocaleString()}</span>
          <QuantityStepper 
            value={quantity} 
            onChange={(val) => onQuantityChange(id, val)} 
          />
        </div>
      </div>
    </div>
  );
}