import React from 'react';
import classNames from 'classnames';
import type { HeadingLevel } from './heading-level-type';
import './heading.scss'; //   CSS Modules  Global SCSS

export type HeadingProps = {
  level?: HeadingLevel;
  children?: React.ReactNode;
  visualLevel?: HeadingLevel;
  inverseColor?: boolean;
  className?: string;
  style?: React.CSSProperties;
};

export function Heading({
  children,
  level = 'h2',
  visualLevel,
  inverseColor = false,
  className,
  style,
}: HeadingProps) {
  const Tag = level as keyof React.JSX.IntrinsicElements;
  
  //   Class String  Prefix 'lm-heading'
  //  visualLevel  Tag 
  const sizeLevel = visualLevel || level;
  
  const headingClasses = classNames(
    'lm-heading',
    `is-${sizeLevel}`, //  is-h1, is-h2
    {
      'is-inverse': inverseColor,
    },
    className
  );

  return (
    <Tag style={style} className={headingClasses}>
      {children}
    </Tag>
  );
}