// src/icon/icon.tsx
import React from 'react';
import { Icon as IconifyRender } from '@iconify/react'; 

export interface IconProps {
  /**  Iconify  "mingcute:home-line" */
  icon?: string; 
  /**  (px) */
  size?: number | string;
  /**  */
  className?: string;
  /**  ( 'currentColor') */
  color?: string;
  /**  */
  style?: React.CSSProperties;
  /**  Component   */
  children?: React.ReactNode;
}

export function Icon({ 
  icon,
  size = 24, 
  className,
  color,
  style,
  children,
  ...rest 
}: IconProps) {
  
  const containerStyle: React.CSSProperties = {
    display: 'inline-flex',
    alignItems: 'center',
    justifyContent: 'center',
    width: size,
    height: size,
    flexShrink: 0,
    color: color || 'currentColor', //  currentColor 
    ...style,
  };

  //  1:  children (Custom SVG  Local Icons)
  if (children) {
    return (
      <span className={className} style={containerStyle} {...rest}>
        {React.isValidElement(children) 
          ? React.cloneElement(children as React.ReactElement<any>, { 
              width: size, 
              height: size,
              //  Error  children  style props
              style: { display: 'block', ...(children.props?.style || {}) } 
            }) 
          : children}
      </span>
    );
  }

  //  2:  icon string 
  if (!icon) return null;

  //  3:  Iconify
  return (
    <span className={className} style={containerStyle} {...rest}>
      <IconifyRender 
        icon={icon} 
        width="100%" 
        height="100%"
        style={{ display: 'block' }}
      />
    </span>
  );
}