import React, { useState } from 'react';
import classNames from 'classnames';
import './image.scss';
import type { ImageProps } from './image.type';

export function Image({
  src,
  alt,
  width,
  height,
  state = 'default',
  padding = 'none',
  radius = 'none',
  fit = 'cover',
  number,
  className,
  style,
  ...rest
}: ImageProps) {
  const [isLoaded, setIsLoaded] = useState(false);

  const rootClasses = classNames(
    'jp-image-root',
    `is-state-${state}`,
    `is-padding-${padding}`,
    `is-radius-${radius}`,
    { 'is-loaded': isLoaded },
    className
  );

  return (
    <div className={rootClasses} style={{ width, height, ...style }}>
      <img
        src={src}
        alt={alt}
        className="jp-image-content"
        onLoad={() => setIsLoaded(true)}
        style={{ objectFit: fit }}
        loading="lazy"
        {...rest}
      />
      
      {state === 'number' && (
        <div className="jp-image-overlay jp-image-number">
          <span>{number}</span>
        </div>
      )}

      {state === 'cant-upload' && (
        <div className="jp-image-overlay jp-image-block">
          <span className="icon"></span>
        </div>
      )}

      {/* Loading Placeholder */}
      {!isLoaded && state !== 'error' && (
        <div className="jp-image-skeleton" />
      )}
    </div>
  );
}