import React from 'react'; //  import React
import classNames from 'classnames';
import './avatar.scss';

export interface AvatarProps {
  src?: string;
  alt?: string;
  name?: string;
  size?: 'sm' | 'md' | 'lg' | number;
  shape?: 'circle' | 'square';
  className?: string;
}

export function Avatar({
  src,
  alt,
  name,
  size = 'md',
  shape = 'circle',
  className,
}: AvatarProps) {
  
  const sizeValue = typeof size === 'number' ? size : undefined;
  
  const getInitials = (name: string) => {
    return name.trim().charAt(0).toUpperCase();
  };

  const renderContent = () => {
    if (src) {
      //   styles.image  'avatar-image' ( scss)
      return <img src={src} alt={alt || name} className="avatar-image" />;
    }
    
    if (name) {
      //   styles.initials  'avatar-initials'
      return <span className="avatar-initials">{getInitials(name)}</span>;
    }

    //   styles.placeholderIcon  'avatar-placeholder-icon'
    return (
      <svg viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" className="avatar-placeholder-icon">
        <path d="M20 21v-2a4 4 0 00-4-4H8a4 4 0 00-4 4v2" />
        <circle cx="12" cy="7" r="4" />
      </svg>
    );
  };

  return (
    <div
      className={classNames(
        'avatar', //   styles.avatar 
        shape,    //   styles[shape]  props ( 'circle'  'square')
        typeof size === 'string' ? size : '', //   styles[size]  'sm', 'md',  'lg'
        className
      )}
      style={sizeValue ? { width: sizeValue, height: sizeValue, fontSize: sizeValue / 2.5 } : {}}
    >
      {renderContent()}
    </div>
  );
}