import React from 'react';
import classNames from 'classnames';
import { Icon } from '@iconify/react';
import './quantity-stepper.scss';

export type QuantityStepperProps = {
  value: number;
  min?: number;
  max?: number;
  onChange: (newValue: number) => void;
  disabled?: boolean;
};

export function QuantityStepper({ 
  value, 
  min = 1, 
  max = 99, 
  onChange, 
  disabled  
}: QuantityStepperProps) {

  const handleDecrement = (e: React.MouseEvent) => {
    e.preventDefault();
    if (value > min && !disabled) {
      onChange(value - 1);
    }
  };

  const handleIncrement = (e: React.MouseEvent) => {
    e.preventDefault();
    if (value < max && !disabled) {
      onChange(value + 1);
    }
  };

  return (
    <div className={classNames('qty-stepper', { 'is-disabled': disabled })}>
      <button 
        type="button" 
        onClick={handleDecrement} 
        disabled={disabled || value <= min}
        className="qty-stepper-button"
        aria-label="Decrease quantity"
      >
        <Icon icon="ic:round-minus" width="20" height="20" />
      </button>
      
      <span className="qty-stepper-value">{value}</span>
      
      <button 
        type="button" 
        onClick={handleIncrement} 
        disabled={disabled || value >= max}
        className="qty-stepper-button"
        aria-label="Increase quantity"
      >
        <Icon icon="ic:round-plus" width="20" height="20" />
      </button>
    </div>
  );
}