import React, { type AnchorHTMLAttributes, type ReactNode } from 'react';
import {
  Link as RouterLink,
  useInRouterContext,
} from 'react-router-dom';
import classNames from 'classnames';
import './link.scss'; //   Global SCSS

export type LinkProps = {
  href?: string;
  external?: boolean;
  noStyles?: boolean;
  target?: string;
  rel?: string;
  children?: ReactNode;
  className?: string;
  disabled?: boolean;
  style?: React.CSSProperties;
  onClick?: (event: React.MouseEvent<HTMLAnchorElement>) => void;
} & Omit<AnchorHTMLAttributes<HTMLAnchorElement>, 'href' | 'onClick'>;

export function Link({
  href = '#',
  external = false,
  noStyles = false,
  children,
  className,
  disabled,
  target,
  rel,
  onClick,
  style,
  ...rest
}: LinkProps) {
  const inRouter = useInRouterContext();

  // 
  const isExternal =
    external ||
    href.startsWith('http') ||
    href.startsWith('mailto') ||
    href.startsWith('tel');

  //   Prefix 'lm-link'  styles object
  const commonClasses = classNames(
    {
      'lm-link': !noStyles,
      'lm-link-no-styles': noStyles,
      'is-disabled': disabled,
    },
    className
  );

  const handleClick = (e: React.MouseEvent<HTMLAnchorElement>) => {
    if (disabled) {
      e.preventDefault();
      return;
    }
    onClick?.(e);
  };

  //  Disabled  <span> 
  if (disabled) {
    return (
      <span className={commonClasses} style={style} aria-disabled="true">
        {children}
      </span>
    );
  }

  //  <a> :  Router Context, ,  Tab 
  if (!inRouter || isExternal || target === '_blank') {
    return (
      <a
        href={href}
        className={commonClasses}
        target={target}
        rel={rel || (target === '_blank' ? 'noopener noreferrer' : undefined)}
        onClick={handleClick}
        style={style}
        {...rest}
      >
        {children}
      </a>
    );
  }

  //  RouterLink  Internal Navigation
  return (
    <RouterLink
      to={href}
      className={commonClasses}
      onClick={handleClick}
      style={style}
      {...(rest as any)}
    >
      {children}
    </RouterLink>
  );
}