import React from 'react';
import classNames from 'classnames';
import './product-card.scss';
import { Link } from '../../navigation/link';

export type ProductCardProps = {
  id: string;
  title: string;
  price: number;
  discountPrice?: number;
  imageUrl: string;
  brand?: string;
  tag?: string;
  isOutOfStock?: boolean;
  href: string;
  onClick?: () => void;
  onAddToCart?: (id: string) => void;
};

export function ProductCard({
  id,
  title,
  price,
  discountPrice,
  imageUrl,
  brand,
  tag,
  isOutOfStock,
  href,
  onClick,
  onAddToCart
}: ProductCardProps) {
  const hasDiscount = discountPrice !== undefined && discountPrice < price;

  const handleCartClick = (e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();
    onAddToCart?.(id);
  };

  return (
    <div className={classNames('product-card', { 'is-out-of-stock': isOutOfStock })}>
      <Link href={href} className="product-card-link-wrapper" noStyles onClick={onClick}>
        <div className="product-card-image-container">
          {tag && <span className="product-card-tag-badge">{tag}</span>}
          <img src={imageUrl} alt={title} className="product-card-image" />
          
          {isOutOfStock && (
            <div className="product-card-overlay">
              <span className="product-card-sold-out-text">OUT OF STOCK</span>
            </div>
          )}

          {!isOutOfStock && onAddToCart && (
            <button 
              className="product-card-quick-add" 
              onClick={handleCartClick}
              aria-label="Add to cart"
            >
              <span className="mingcute--shopping-cart-1-line" />
            </button>
          )}
        </div>

        <div className="product-card-content">
          {brand && <span className="product-card-brand">{brand}</span>}
          <h3 className="product-card-title">{title}</h3>
          
          <div className="product-card-price-container">
            {hasDiscount ? (
              <>
                <span className="product-card-discount-price">{discountPrice?.toLocaleString()}</span>
                <span className="product-card-original-price">{price.toLocaleString()}</span>
              </>
            ) : (
              <span className="product-card-price">{price.toLocaleString()}</span>
            )}
          </div>
        </div>
      </Link>
    </div>
  );
}