import React from 'react';
import classNames from 'classnames';
import './paragraph.scss'; //   CSS Modules  Global SCSS

export type ParagraphProps = {
  children?: React.ReactNode;
  /**
   * The HTML element to render the paragraph as.
   * Defaults to 'p'.
   */
  element?: keyof React.JSX.IntrinsicElements;
  /**
   * Size of the text.
   * Defaults to 'body-default'.
   */
  size?: 'body-large' | 'body-medium' | 'body-default' | 'body-small' | 'caption';
  /**
   * Color of the text.
   * Defaults to 'primary'.
   */
  color?: 'primary' | 'secondary' | 'tertiary' | 'inverse';
  className?: string;
  style?: React.CSSProperties;
} & React.HTMLAttributes<HTMLElement>;

export function Paragraph({
  children,
  element = 'p',
  size = 'body-default',
  color = 'primary',
  className,
  style,
  ...rest
}: ParagraphProps) {
  const Component = element as React.ElementType;

  //   Class String  Prefix 'lm-paragraph'
  const paragraphClasses = classNames(
    'lm-paragraph',
    `is-size-${size}`,
    `is-color-${color}`,
    className
  );

  return (
    <Component
      {...rest}
      style={style}
      className={paragraphClasses}
    >
      {children}
    </Component>
  );
}