import React from 'react';
import classNames from 'classnames';
import './checkout-stepper.scss';

export type Step = {
  id: number;
  label: string;
};

export type CheckoutStepperProps = {
  steps: Step[];
  currentStep: number; // 1, 2, 3
};

export function CheckoutStepper({ steps, currentStep }: CheckoutStepperProps) {
  return (
    <div className="stepper-container"> {/*   container */}
      {steps.map((step, index) => (
        <React.Fragment key={step.id}>
          <div className={classNames('stepper-step', { //   styles.step
            'active': currentStep === step.id,         //   styles.active
            'completed': currentStep > step.id         //   styles.completed
          })}>
            <div className="stepper-circle"> {/*   prefix */}
              {currentStep > step.id ? (
                <span className="mingcute--check-line" />
              ) : (
                step.id
              )}
            </div>
            <span className="stepper-label">{step.label}</span>
          </div>
          {index < steps.length - 1 && <div className="stepper-line" />}
        </React.Fragment>
      ))}
    </div>
  );
}