import React from 'react';
import classNames from 'classnames';
import { Link } from '../../navigation/link/link';
import { Icon } from '../../../icon/icon';
import './breadcrumbs.scss';

export type BreadcrumbItem = {
  label: string;
  href?: string;
};

export type BreadcrumbsProps = {
  items: BreadcrumbItem[];
  className?: string;
};

export function Breadcrumbs({ items, className }: BreadcrumbsProps) {
  return (
    <nav 
      aria-label="Breadcrumb" 
      className={classNames('lm-breadcrumbs', className)}
    >
      <ol className="lm-breadcrumbs-list">
        {items.map((item, index) => {
          const isLast = index === items.length - 1;

          return (
            <li key={index} className="lm-breadcrumbs-item">
              {!isLast && item.href ? (
                <Link href={item.href} className="lm-breadcrumbs-link">
                  {item.label}
                </Link>
              ) : (
                <span className="lm-breadcrumbs-current" aria-current="page">
                  {item.label}
                </span>
              )}
              
              {!isLast && (
                <span className="lm-breadcrumbs-separator" aria-hidden="true">
                  <Icon icon="mingcute:right-line" size={14} />
                </span>
              )}
            </li>
          );
        })}
      </ol>
    </nav>
  );
}